<template>
	<div class="main">
		<!--头部-->
		<header class="header">
			<div class="header-left">
				<i class="fa fa-bars" @click="themesViewShow" aria-hidden="true"></i>
				<span class="home">首页</span>
			</div>
			<div class="header-left">
				<i class="fa fa-bell" aria-hidden="true" @click="goLogin"></i>
				<i class="fa fa-ellipsis-v" aria-hidden="true"></i>
			</div>
		</header>
		<!--图片切换-->
		<home-slider :topStories="topStories"></home-slider>
		<!--文章列表-->
		<news-list :stories="stories"></news-list>
		<!--左侧边栏-->
		<sidebar :sidebarState="sidebarState"></sidebar>
		<div class="sidebar-mask" v-show="sidebarState" @click="themesViewHide"></div>
	</div>
</template>

<script>
import HomeSlider from './HomeSlider'
import NewsList from './NewsList'
import Sidebar from './Sidebar'
export default {
  name: 'Home',
  components: {
    'HomeSlider': HomeSlider,
    'NewsList': NewsList,
    'Sidebar': Sidebar
  },
  data () {
    return {
      topStories: '',
      stories: '',
      swiperOption: {
        autoplay: 3500,
        setWrapperSize: true,
        pagination: '.swiper-pagination',
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true
      },
      sidebarState: false
    }
  },
  created () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      this.$http.get('https://zhihu-daily.leanapp.cn/api/v1/last-stories').then(function (data) {
        this.topStories = data.body.STORIES.top_stories
        this.stories = data.body.STORIES.stories
        console.info(data)
      }, function (response) {
        console.log('请求失败')
      })
    },
    themesViewShow () {
      this.sidebarState = true
    },
    themesViewHide () {
      this.sidebarState = false
    },
    goLogin () {
      this.$router.push({path: '/login'})
    }
  }
}
</script>

<style scoped>
.main {
	position: relative;
}
.header .fa-bars,
.header .fa-ellipsis-v {
	padding: .4rem;
}
.header .home,
.header .fa-bell {
	padding: .4rem .3rem;
}
.sidebar-mask {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.3);
}
</style>



