<template>
	<div id="sidebar" class="sidebar" :class="[sidebarState ? 'fadeIn' : 'fadeOut']">
		<!--左侧边栏内容-->
		<div class="sidebar-content">
			<!--个人信息-->
			<div class="personal">
				<div class="avatar">
					<img :src="avatarUrl" /><span>Ciel</span>
				</div>
				<div class="info">
					<div class="info-left">
						<i class="fa fa-star" aria-hidden="true"></i><span class="favorite">我的收藏</span>
					</div>
					<div class="info-right">
						<i class="fa fa-download" aria-hidden="true"></i><span class="favorite">离线下载</span>
					</div>
				</div>
			</div>
			<!--首页-->
			<div class="go-home" @click="themesViewHide">
				<i class="fa fa-home" aria-hidden="true"></i><span class="home">首页</span>
			</div>
			<!--主题列表-->
			<div class="themes-view">
			  <li class="themes-list" v-for="(item,key) in themes" @click="themesContent(key)">
			    <span>{{item.name}}</span>
			    <i class="fa fa-plus" aria-hidden="true"></i>
			  </li>
			</div>
		</div>
	</div>
</template>

<script>
import avatar from '../assets/img/avatar.jpg'
export default {
  props: ['sidebarState'],
  name: 'sidebar',
  data () {
    return {
      avatarUrl: avatar,
      themes: '',
      themesId: ''
    }
  },
  created () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      this.$http.get('https://zhihu-daily.leanapp.cn/api/v1/themes').then(function (data) {
        this.themes = data.body.THEMES.others
      }, function (response) {
        console.log('请求失败')
      })
    },
    themesViewShow () {
      this.$store.state.themesViewState = true
    },
    themesViewHide () {
      this.$store.state.themesViewState = false
      this.$router.push({path: '/'})
    },
    themesContent (index) {
      this.$store.state.themesViewState = false
      this.themesId = JSON.stringify(this.themes[index].id)
      this.$router.push({path: '/themesContent/', query: {themesId: this.themesId}})
    }
  }
}
</script>

<style scoped>
.sidebar {
	position: fixed;
	top: 0;
	left: -88%;
	z-index: 1000;
	width: 88%;
	height: 100%;
	overflow: auto;
}
.sidebar.fadeIn {
	left: 0;
	transition:left 1s;
	-moz-transition:left 1s; /* Firefox 4 */
	-webkit-transition:left 1s; /* Safari and Chrome */
	-o-transition:left 1s; /* Opera */
}
.sidebar.fadeOut {
	left: -88%;
	transition:left 1s;
	-moz-transition:left 1s; /* Firefox 4 */
	-webkit-transition:left 1s; /* Safari and Chrome */
	-o-transition:left 1s; /* Opera */
}
.personal {
	width: 100%;
	height: 3rem;
	background-color: #00a2ea;
	color: #fff;
}
.avatar {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-bottom: .3rem;
	padding: .3rem .5rem;
}
.avatar img {
	margin-right: .5rem;
	width: 1.2rem;
	height: 1.2rem;
	border-radius: 100%;
}
.avatar span {
	font-size: .5rem;
}
.info {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-left: .8rem;
}
.favorite, .download {
	padding: .3rem .5rem;
}
.go-home {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	width: 100%;
	height: 1.2rem;
	background-color: #eee;
	color: #00a2ea;
}
.fa-home {
	padding: 0 .5rem;
}
.themes-view {
	width: 100%;
	height: auto;
	background-color: #fff;
}
.themes-list {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: .4rem 1rem .4rem .5rem;
}
.fa-plus {
	color: #ccc;
}
</style>



