<template>
  <div class="themes-content">
    <!--头部-->
    <header class="header">
      <div class="header-left">
        <i class="fa fa-bars" @click="themesViewShow" aria-hidden="true"></i>
        <span class="home">{{themesContent.name}}</span>
      </div>
      <div class="header-right">
        <i class="fa" :class="{'fa-plus-square-o': hasWatch, 'fa-minus-square-o': !hasWatch }" aria-hidden="true" @click="addWatch"></i>
      </div>
      <div class="watch-tip" :class="{fadeInUp:this.tipWatchVal}" v-show="this.tipWatchVal"><span>{{tipWatchText}}</span></div>
    </header>
    <!--背景图片-->
    <div class="top-stories-view" :style="{backgroundImage: 'url(' + themesContent.background + ')'}">
    	<h2 class="img-title">{{themesContent.description}}</h2>
    </div>
    <!--内容列表-->
    <news-list :stories="themesContent.stories" :editorsData="themesContent.editors"></news-list>
    <!--左侧边栏-->
    <sidebar :sidebarState="sidebarState"></sidebar>
    <div class="sidebar-mask" v-show="sidebarState" @click="themesViewHide"></div>
  </div>
</template>

<script>
import NewsList from './NewsList'
import Sidebar from './Sidebar'
export default {
  name: 'themesContent',
  components: {
    'NewsList': NewsList,
    'Sidebar': Sidebar
  },
  data () {
    return {
      themesContent: '',
      themeId: '',
      hasWatch: true,
      tipWatchVal: '',
      tipWatchText: '已关注',
      sidebarState: ''
    }
  },
  created () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      this.themeId = window.location.href.split('?')[1].split('=')[1]
      this.$http.get('https://zhihu-daily.leanapp.cn/api/v1/themes/' + this.themeId).then(function (data) {
        console.log(data)
        this.themesContent = data.body.THEMEDES
      }, function (response) {
        console.log('请求失败')
      })
    },
    themesViewShow () {
      this.sidebarState = true
    },
    themesViewHide () {
      this.sidebarState = false
    },
    addWatch () {
      this.tipWatchVal = true
      if (this.hasWatch) {
        this.hasWatch = false
        this.tipWatchText = '已关注'
      } else {
        this.hasWatch = true
        this.tipWatchText = '已取消关注'
      }
      this.tipWatchHide()
    },
    tipWatchHide () {
      const _this = this
      setTimeout(function () { _this.tipWatchVal = false }, 2000)
    }
  },
  computed: {
    themesView () {
      return this.$store.state.themesViewState
    }
  }
}
</script>

<style scoped>
.header .fa {
	padding: .4rem;
}
.header-right .fa {
  font-size: .6rem;
}
.watch-tip {
  position: fixed;
  top: 80%;
  left: 50%;
  z-index: 1001;
  margin-left: -1.5rem;
  width: 3rem;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: .2rem;
  opacity: 0;
}
.watch-tip.fadeInUp {
  opacity: 1;
  animation: fadeInUp 3s;
}
@keyframes fadeInUp {
	0% {
		opacity: 1;
		transform: translate3d(0, 100%, 0)
	}
	10% {
	  opacity: 1;
		transform: none
	}
  15% {
    opacity: 1;
    transform: translate3d(0, 5%, 0)
	}
  100% {
    opacity: 0;
  }
}
.watch-tip span {
  font-size: .3rem;
}
.top-stories-view {
	position: relative;
	margin-top: 1.5rem;
  height: 6rem;
}
.img-title {
	position: absolute;
	bottom: 10px;
	padding: .3rem .4rem;
	color: #fff;
	font-size: .6rem;
	text-align: left;
}
.sidebar-mask {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.3);
}
</style>



